// stylelint-disable declaration-block-single-line-max-declarations
.highlight {
  padding: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  overflow: auto;
  background-color: #f8f9fa;
  border-radius: 5px;

  pre,
  code {
    display: block;
    padding: 0;
    margin: 0;
    white-space: unset;
  }
}

.master-widget {
  .highlight {
    padding: 0;
    margin: 0;
    background: transparent;
  }
}

.highlighter-rouge{
  color: $theme-color-default;
}

// stylelint-disable
code[class*="language-"],
pre[class*="language-"] {
  hyphens: none;
  line-height: 1.5;
  color: $black;
  text-align: left;
  word-break: normal;
  word-spacing: normal;
  word-wrap: normal;
  tab-size: 2;
  white-space: pre;
  background: none;
}

pre[class*="language-"] {
  overflow: auto;
}

